* {
  margin: 0;
  padding: 0;
}

#container {
  margin-top: 2vh;
}

header {
  text-align: center;
}

header h1 {
  font-size: 1.5rem;
}

header, article {
  width: 80vw;
  margin: 0 3vw;
  padding: 1.3rem 7vw;
}

#text {
  font-size: 1.4rem;
  text-align: center;
}

@media (min-width: 500px) {
  #container {
    margin-top: 10vh;
  }
  header h1 {
    font-size: 2.5rem;
  }
  #text {
    font-size: 1.7rem;
  }
}

@media (min-width: 700px) {
  header h1 {
    font-size: 3rem;
  }
  #text {
    font-size: 2rem;
  }
}

#author {
  margin-top: 1.5rem;
  text-align: right;
  font-weight: bold;
}

.buttons {
  margin-top: 2.5rem;
}

#tweet-quote {
  padding: 1.2rem 0.8rem 0.5rem;
}

button {
  margin-top: -0.55rem;
  padding: 1rem 1.5rem;
  float: right;
  border: 0;
}

article, button, a {
  border-radius: 5px;
}

footer {
  text-align: center;
  margin: 1rem auto;
}